<template>
  <div>
    <el-button @click="showChild">打开子组件1</el-button>
    <el-button @click="showChild2">打开子组件2</el-button>
    <Child :isShow="isShow" @cancel="cancel" @submit="submit"></Child>
    <Child2
      :visible.sync="isShow2"
      @cancel2="cancel2"
      @submit2="submit2"
    ></Child2>
  </div>
</template>

<script>
import Child from "./components/child.vue";
import Child2 from "./components/child2.vue";
export default {
  name: "page4",
  components: {
    Child,
    Child2
  },
  data() {
    return {
      isShow: false,
      isShow2: false
    };
  },
  methods: {
    // 组件一
    showChild() {
      this.isShow = true;
    },
    cancel(val) {
      this.isShow = val;
    },
    submit(val) {
      this.isShow = val;
    },
    // 组件二
    showChild2() {
      this.isShow2 = true;
    },
    cancel2(val) {
      this.isShow2 = val;
    },
    submit2(val) {
      this.isShow2 = val;
    }
  },
  created() {}
};
</script>
<style lang="less" scoped></style>



