<template>
  <div>
    <el-dialog
      title="提示"
      :visible.sync="isShow"
      width="30%"
      :before-close="
        () => {
          $emit('cancel', false);
        }
      "
    >
      <span>我是子组件一</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="$emit('cancel', false)">取 消</el-button>
        <el-button type="primary" @click="$emit('submit', false)"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "child",
  props: {
    isShow: Boolean,
    default: () => {
      return false;
    }
  },
  data() {
    return {};
  },
  methods: {
    handleClose(done) {
      done();
    }
  },
  created() {}
};
</script>
<style lang="less" scoped></style>



