<template>
  <div>
    <el-dialog
      title="提示"
      :visible.sync="show2"
      width="30%"
      @close="
        () => {
          $emit('update:visible', false);
        }
      "
    >
      <span>我是子组件二</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="$emit('cancel2', false)">取 消</el-button>
        <el-button type="primary" @click="$emit('submit2', false)"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "child",
  props: {
    visible: Boolean,
    default: () => {
      return false;
    }
  },
  watch: {
    visible(val) {
      this.show2 = val;
    }
  },
  data() {
    return {
      show2: this.visible
    };
  },
  methods: {},
  created() {}
};
</script>
<style lang="less" scoped></style>



